
.popup{
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, .5);
    margin: 0;
    opacity: 0;
    visibility: hidden;
    transition: opacity 1s,visibility 1s;
}